
import { defineComponent, reactive } from 'vue'
// import VueRouter from 'vue-router'
import router from './../router'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false
    })
    const toPage = () => {
      if (Number(router.currentRoute.value.query.type) === 1) {
        router.push('/order')
      } else {
        router.push('/correct-list')
      }
    }
    const toPageReturn = () => router.go(-1)
    return {
      state,
      toPage,
      toPageReturn
    }
  }
})
