<template>
  <div class="g-hello-world-box">
    <van-nav-bar
      class="m-nav"
      title="选民说事，代表督事"
      left-arrow
      @click-left="toPageReturn"
    />
    <img class="u-bg" src="./../assets/logo.png">
    <van-button style="width: 30%;margin-top: 10rem" round block type="primary" native-type="submit" @click="toPage">选民说事></van-button>
    <p class="u-text">民有所呼，我有所应</p>
    <div class="u-box">
      <p class="u-tt">【选民说事】范围：</p>
      <p class="u-text">反映事项包括街道村建设、</p>
      <p class="u-text">文明创建、农业生产、食品安全、</p>
      <p class="u-text">教育医疗、公共服务、生态环保等问题。</p>
    </div>
    <van-form style="width: 100%;">
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit" @click="toPage">
          进入首页
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'
// import VueRouter from 'vue-router'
import router from './../router'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false
    })
    const toPage = () => {
      if (Number(router.currentRoute.value.query.type) === 1) {
        router.push('/order')
      } else {
        router.push('/correct-list')
      }
    }
    const toPageReturn = () => router.go(-1)
    return {
      state,
      toPage,
      toPageReturn
    }
  }
})
</script>

